:root {
    --default-blue: #0074DF;
}

body {
    font-family: 'Inter';
    margin: 0;
    color: #222;
}

/* AHAHA MY OWN BOOTSTRAP WITH BLACKJACK AND SLUTS */
.muted {
    color: gray;
}

.col-1 {
    display: block;
    width: 7.5%
}

.col-2 {
    display: block;
    width: 15%
}

.col-3 {
    display: block;
    width: 25%;
}

.col-4 {
    display: block;
    width: 32%;
}

.col-5 {
    display: block;
    width: 40%;
}

.col-6 {
    display: block;
    width: 50%;
}

.col-9 {
    display: block;
    width: 75%;
}

.col-12 {
    display: block;
    width: 100%;
}

.p-default {
    padding: .5rem;
}

.m-default {
    margin: .5rem;
}

.m-0 {
    margin: 0;
}

.p-0 {
    padding: 0;
}

.h-25 {
    height: 25%;
}

.h-50 {
    height: 50%;
}

.h-75 {
    height: 75%;
}

.h-100 {
    height: 100%;
}

.w-10 {
    width: 10%;
}

.w-25 {
    width: 25%;
}

.w-50 {
    width: 50%;
}

.w-75 {
    width: 75%;
}

.w-85 {
    width: 85%;
}

.w-90 {
    width: 90%;
}

.w-100 {
    width: 100%;
}

.w-fit {
    width: fit-content;
    block-size: fit-content;
}

.text-center {
    text-align: center;
}

.center {
    margin: auto;
}

.d-flex {
    display: flex;
}

/* buttons are different almost everywhere */
.btn {}

.btn-default {
    color: white;
    background-color: var(--default-blue);
    padding: 1rem;
    padding-left: 3rem;
    padding-right: 3rem;
    border: 0;
    border-radius: 5px;
}

.btn:hover {
    background-color: var(--default-blue);
    color: white;
    cursor: context-menu;
}

/* normal css, not mini bootstrap */
.round-left {
    border-radius: 12px 0 0 12px;
}

.round-right {
    border-radius: 0 12px 12px 0;
}

.logo {
    height: 4rem;
    width: 12rem;
}

.nodecor {
    text-decoration: none;
}

.underlined:hover {
    text-decoration: none;
    border-bottom: 2px solid white;
}

.underlined {
    text-decoration: none;
    border-bottom: 2px solid transparent;
}

.underlined-blue:hover {
    text-decoration: none;
    border-bottom: 2px solid var(--default-blue);
}

.underlined-blue {
    text-decoration: none;
    border-bottom: 2px solid transparent;
}

.underlined-always {
    text-decoration: none;
    border-bottom: 2px solid white;
}

.underlined-blue-always {
    text-decoration: none;
    border-bottom: 2px solid var(--default-blue);
}

.underdashed:hover {
    text-decoration: none;
    border-bottom: 2px dashed white;
}

.underdashed-blue:hover {
    text-decoration: none;
    border-bottom: 2px dashed var(--default-blue);
}

.underdashed-always {
    text-decoration: none;
    border-bottom: 2px dashed white;
}

.underdashed-blue-always {
    text-decoration: none;
    border-bottom: 2px dashed var(--default-blue);
}

hr {
    border: 1px #ccc solid;
}

/* main styles */

.dot-container {
    width: 1rem;
    height: 1rem;
    padding: .25rem;
}

.dot {
    background-color: white;
    border-radius: 50%;
    width: 75%;
    height: 75%;
}

.dot-selected {
    background-color: #FFA41B;
    border-radius: 50%;
    width: 75%;
    height: 75%;
}

.dotted-left {
    background-image: url(images/dots.png);
    background-size: 6rem 8rem;
    background-repeat: no-repeat;
    background-position: 3% 20%;
}

.dotted-right {
    background-image: url(images/dots2.png);
    background-size: 4rem 6rem;
    background-repeat: no-repeat;
    background-position: 95% 90%;
}

.card {
    background-color: white;
    border: 0;
    border-radius: 12px;
    padding: 1rem;
}

.icon-def {
    width: 1.5rem;
    height: 1.5rem;
    padding: 1rem;
}

.header a {
    color: black;
    text-decoration: none;
}

.nav-header {
    padding: 1.5rem;
    padding-left: 4rem;
    padding-right: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.can-float {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-right {
    display: flex;
    float: right;
    padding-top: 1rem;
}

.nav-right a {
    margin: .5rem
}

.nav-center {
    display: flex;
    margin: auto;
    padding-top: 1rem;
}

.nav-center a {
    margin: .5rem
}

.nav-left {
    display: flex;
    float: left;
    margin-top: .5rem;
}

.block {
    padding-left: 10%;
    padding-right: 10%;
    width: 80%;
    padding-top: 15vh;
    padding-bottom: 15vh;
}

.flex-block {
    padding-left: 10%;
    padding-right: 10%;
    width: 80%;
    display: flex;
}

/*.block-inner { margin-top: 15vh; margin-bottom: 15vh; width: 100%; }*/

.home-head {
    background-image: url(images/bg.png);
    color: white;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
}

.home-head a {
    color: white;
    text-decoration: none;
}

.home-head-form {
    display: flex;
}

.home-head-form button {
    background-color: #FFA41B;
    padding-left: 0.5rem;
    border: 0;
    border-radius: 8px;
    color: white;
}

.home-head-form input {
    padding: 1rem;
    height: 2rem;
}

.home-head-form .right-field {
    margin-right: 2rem;
}

.promo-block {
    width: 50%;
    height: 50%;
    padding: 1rem;
}

.promo-block h4 {
    font-size: 4rem;
    color: var(--default-blue);
    margin: 0;
    font-weight: 300;
}

.promo-block .selected {
    color: var(--default-blue);
}

.solutions-block {
    width: 50%;
    padding: 0.5rem;
}

.solutions-inner {
    border-radius: 12px;
    border: 0;
    background-color: #F8F8F8;
    padding: 1rem;
}

.solutions-inner button {
    width: 100%;
    border-radius: 5px;
    padding: 1rem;
    border: 1px var(--default-blue) solid;
    background-color: transparent;
    color: var(--default-blue);
}

.sols-point {
    width: 1.5rem;
    height: 1.5rem;
    padding: .5rem;
}

.sols-desc {
    margin: 0;
    padding: .5rem;
}

.sols-top {
    display: flex;
}

.sols-top h3 {
    color: var(--default-blue);
    font-size: 1.5rem;
    margin: 0;
    font-weight: 400;
    line-height: 2rem;
}

.sols-top p {
    margin: 0;
    line-height: 1.5rem;
}

.sols-top img {
    width: 7rem;
    height: 7rem;
    padding-bottom: 3rem;
}

.sols-top .inner {
    padding-left: 2rem;
}

.quest-head {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
}

.quest-head img {
    float: left;
    width: 2.5rem;
    height: 2.5rem;
}

.quest-dropdown {
    width: 100%;
    background-color: white;
    border-radius: 12px;
    margin: .5rem;
}

.quest-dropdown p {
    padding-left: 1rem;
}

.quest-content {
    padding: 1rem;
}

.serv-inner {
    width: 25%;
    padding: 1rem;
}

.serv-card {
    background-color: #F8F8F8;
    border-radius: 12px;
    padding: 1rem;
    padding-top: 4rem;
    text-align: center;
}

.serv-card button {
    width: 100%;
    background-color: transparent;
    border-radius: 5px;
    padding: 1rem;
    border: 1px var(--default-blue) solid;
    color: var(--default-blue);
}

.ask-block {
    color: white;
    background-image: url(./images/ask-bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.ask-block button {
    color: white;
    background-color: var(--default-blue);
    border-radius: 5px;
    border: 0;
    padding: 1rem;
    padding-right: 3rem;
    padding-left: 3rem;
    margin: auto;
}

.ask-input {
    background-color: #ffffff10;
    border: 1px solid #ffffff80;
    border-radius: 8px;
    width: 80%;
    padding: .5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-top: .5rem;
}

.ask-input::placeholder {
    color: #ccc;
}

.geo-cities {
    color: var(--default-blue);
    background-color: #0074DF10;
    border: 1px solid #0074DF80;
    border-radius: 15px;
    padding: .5rem;
    margin: .5rem;
}

.cost-inner {
    width: 25%;
    padding: 1rem;
}

.cost-card {
    background-color: white;
    border-radius: 12px;
    padding: 1rem;
    padding-top: 4rem;
    text-align: center;
}

.cost-card button {
    width: 100%;
    background-color: transparent;
    border-radius: 5px;
    padding: 1rem;
    border: 1px var(--default-blue) solid;
    color: var(--default-blue);
}

.about-right a {
    text-decoration: none;
    color: black;
    padding: .5rem;
    padding-left: 1rem;
    padding-right: 1rem;
}

.advs {
    padding: .5rem;
}

.advs img {
    width: 3rem;
    height: 3rem;
}

.advs p {
    font-size: 1.5rem;
    margin: 0;
    padding: .75rem;
}

.advs-menu {
    list-style-type: none;
}

.advs-menu li {
    padding: .5rem;
}

.advs-menu li::before {
    content: "\2022";
    color: var(--default-blue);
    font-weight: bold;
    display: inline-block;
    width: 1em;
}

.advs-text {
    margin-top: 1.5rem;
}

.calc-input {
    background-color: #F8F8F8;
    border: 1px solid gainsboro;
    border-radius: 5px;
    padding: .5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.calc-table table {
    width: 100%;
}

.calc-table tr {
    border-bottom: 1px solid gainsboro;
    height: 4.5rem;
}

.calc-table th,
.calc-table td {
    width: 25%;
    text-align: left;
    border-bottom: 1px solid gainsboro;
}

.calc-table button {
    border: 1px solid var(--default-blue);
    border-radius: 5px;
    padding: .75rem;
    width: 100%;
    font-weight: 400;
    color: var(--default-blue);
    background-color: transparent;
}

.sender-table table {
    width: 100%;
}

.sender-table tr {
    border-bottom: 1px solid gainsboro;
    height: 4.5rem;
}

.sender-table th,
.sender-table td {
    text-align: left;
    border-bottom: 1px solid gainsboro;
}

.sender-table button {
    border: 1px solid var(--default-blue);
    border-radius: 5px;
    padding: .75rem;
    width: 100%;
    font-weight: 400;
    color: var(--default-blue);
    background-color: transparent;
}

.inv-row {
    border-bottom: 1px solid gainsboro;
    width: 100%;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.inv-row .item {
    padding-left: 1rem;
    padding-right: 1rem;
}

.avatar {
    border-radius: 50%;
    background-color: var(--default-blue);
    color: white;
    width: 2rem;
    height: 2rem;
}

.avatar .inner {
    padding: .5rem;
    margin: 0;
    width: 1rem;
    height: 1rem;
    text-align: center;
}

footer.delexFooter {
    padding: 6rem 16rem;
    display: flex;
    justify-content: space-between;
}
footer.delexFooter > div {
    display: flex;
    flex-direction: column;
}
footer.delexFooter > div > .footer-list {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.25rem
}
footer.delexFooter > div > h6 {
    color: #222;
    font-size: 1.25rem;
    font-weight: 600;
    margin: 0;
    margin-bottom: 2rem;
}

footer.delexFooter > .footer-legal {
    color: #888;
    font-weight: 400;
    justify-content: space-between;
}

#question-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    align-self: center;
    height: 100vh;
    width: 100%;
    background-image: url("./images/ask-bg.png");
    color: #FFF;
    gap: 20px
}

#question-section > h1 {
    font-size: 36px;
    font-weight: 400;
    margin: 0;
}
#question-section > h2 {
    font-size: 18px;
    font-weight: 400;
    margin: 0;
    margin-bottom: 40px;
}

#question-section > form {
    display: flex;
    flex-direction: column;
    gap: 40px;
    width: 790px;
}

#question-section > form > div {
    display: flex;
    flex-direction: column;
    gap: 30px;
}
#question-section > form > div > .question-contact-info {
    display: flex;
    gap: 60px;
    align-items: stretch;
    align-content: stretch;
}

#question-section > form > input[type="submit"] {
    background-color: #0074DF;
    border: 0;
    height: 70px;
    width: 300px;
    align-self: center;
    border-radius: 5px;
    color:#FFF;
    font-weight: 600; 
}

.question-input {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}
.question-input > input, .question-input > textarea {
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    background: rgba(255, 255, 255, 0.10);
    backdrop-filter: blur(2.5px); 
    width: 100%;
    color: #fff;
    padding: 20px;
}
.question-input > input::placeholder, .question-input > textarea::placeholder {
    color:#FFF;
    opacity: 0.6; 
}
.question-input > textarea {
    height: 156px;
}

.hidden {
    display: none;
}

.aboutCompany-image {
    background: url("images/image\ \(2\).png") no-repeat;
    background-position: right;
    width: 640px;
    height: 480px;
    flex-shrink: 0;
}

.about-tabs {
    display: flex;
    gap: 53px;
    font-size: 18px; 
    font-weight: 400; 
    color: #222;
    height: 100%;
    align-self: stretch;
}
.about-tabs .tab {
    color: #888;
    height: 100%;
    border-bottom: 2px #0074DF00 solid;
    height: 34px;
}
.about-tabs .tab.tab-active{
    color: #222;
    border-color: #0074DF;
}

.about-header {
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin-bottom: 50px;
}
.about-header > h1 {
    margin: 0;
    font-weight: 400;
    font-size: 40px;
}
.about-plusimg {
    display: flex;
    gap: 2rem;
    font-weight: 400;
    line-height: 160%;
}

.about-plusimg .info > h2 {
    font-size: 20px;
    font-weight: 500;
    margin-top: 0;
}

.about-cointainer {
    padding-left: 240px;
    padding-right: 240px;
    padding-top: 40px;
    padding-bottom: 150px;
    background: #F8F8F8;
}

.navigation {
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 40px;
}

.about-spoilers > details {
    display: flex;
    flex-direction: row;
    padding: 40px 70px;
    border-top: 1px solid #E0E0E0;
}
.about-spoilers > details:last-of-type {
    border-bottom: 1px solid #E0E0E0;
}

.about-spoilers > details > summary {
    font-weight: 500; 
    font-size: 20px;
}

.about-spoilers > details > ul {
    position: relative;
    top: -16px;
    margin: 0;
    left: 50%;
    display: flex;
    flex-direction: column;
    font-weight: 400; 
    gap: 15px;
}

.about-button-container {
    display: flex;
    align-items: stretch;
    align-content: stretch;
    gap: 20px;
}
.about-button {
    /*there will be a lot of buttons like that prob make it more generic will be better idea*/
    width: 100%;
    padding: 20px;
    border: none;
    background-color: #0074DF;
    color: #fff;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.about-button::before {
    display: block;
    content: none;
    background-position: center;
    width: 36px;
    height: 36px;
}

.about-button.icon-button-calc::before {
    background: url("images/noun-calculator-1115604\ 1.svg") no-repeat center;
    content: "";
}

.about-button.icon-button-offers::before {
    background: url("images/noun-layers-1346016\ 1.svg") no-repeat center;
    content: "";
}

.about-after-spoilers {
    margin-top: 70px;
}

#about-misson > h2 {
    color: #FFA41B;
    font-size: 14px; 
    font-weight: 500;
}

#about-misson > h1 {
    color: #222;
    font-size: 30px;
    font-weight: 400;
    line-height: 150%; /* 45px */ 
}
#about-misson > h1, #about-misson > h2 {
    margin: 0;
    width: fit-content;
    text-align: center;
}

.spacedtext {
    letter-spacing: 4.2px;
    text-transform: uppercase; 
}

#about-misson {
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: center;
    align-items: center;
    padding: 70px 240px;
}

@media screen and (max-width: 1366px) {
    .block {
        padding-top: 0;
    }
    #question-section {
        height: 130vh;
    }
    footer.delexFooter {
        padding: 6rem 8rem;
    }
    #about-misson, .about-cointainer {
        padding-left: 6rem;
        padding-right: 6rem;
    }
    .aboutCompany-image {
        height: calc(480px - 48px);
        width: calc(640px - 64px);
    }
}